<template>
  <view class="page">

    <view class="address-item" @click="editAddress('edit')">
      <view class="receiver">
        <view class="name">张小蕊</view>
        <view class="default">默认</view>
      </view>
      <view class="mobile">17737110577</view>
      <view class="address">河南省郑州市高新区科学大道西四环万科城三期河南省郑州市高新区科学大道西四环万科城三期</view>
    </view>

    <view class="address-item" @click="editAddress('edit')">
      <view class="receiver">
        <view class="name">张小蕊</view>
      </view>
      <view class="mobile">17737110577</view>
      <view class="address">河南省郑州市高新区科学大道西四环万科城三期</view>
    </view>

    <view class="add-btn" @click="editAddress('add')">
      <uni-icons type="plusempty" size="22" color="" />
      新增地址
    </view>

  </view>
</template>

<script>
  export default {
    data() {
      return {

      };
    },
    methods: {
      editAddress(type) {
        if (type === 'edit') {
          const address_info = {
            receiver: '张小蕊',
            mobile: '17737110577',
            area: ['1-0', '1-2', '111'],
            add_str: '河南省郑州市高新区科学大道西四环万科城三期河南省郑州市高新区科学大道西四环万科城三期',
            is_default: true
          }
          uni.setStorageSync('user_address', JSON.stringify(address_info))
          uni.navigateTo({
            url: '/mypkg/address-edit/address-edit'
          })
        } else {
          uni.removeStorageSync('user_address')
          uni.navigateTo({
            url: '../address-edit/address-edit'
          })
        }
      }
    }
  }
</script>

<style lang="scss">
  .page {
    padding-top: 30rpx;
